import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import resourcesToBackend from 'i18next-resources-to-backend';
import { useEffect, useState } from 'react';
import { fetchLocalization } from '../../services/localization-fetcher';
let init = i18n
    .use(resourcesToBackend((language, namespace, callback) => fetchLocalization(language)
    .then((localization) => callback(null, localization.strings))
    .catch((e) => callback(e, null))))
    .use(initReactI18next)
    .init({
    partialBundledLanguages: true,
    resources: {},
    fallbackLng: 'en',
    debug: process.env.NODE_ENV === 'development',
    ns: 'translation',
    defaultNS: 'translation',
    interpolation: {
        escapeValue: false,
    },
});
export const useI18n = ({ language }) => {
    const [initialized, setInitialized] = useState(false);
    useEffect(() => {
        if (initialized) {
            return;
        }
        init.then(() => setInitialized(true));
    }, [initialized]);
    useEffect(() => {
        init = init.then(() => i18n.changeLanguage(language));
    }, [language]);
    return { initialized };
};
